<template>
</template>

<script>
	export default {
		// data() {
		// 	return {
		// 		//currentProjectInfo: {},
		// 		// piShifts : ["初盘", "复盘", "三盘"],
		// 		// currentShift : 0,
		// 		// piModes : ["按库位", "按商品", "全部"],
		// 		// currentMode : 0
		// 	}
		// },
		onLoad(){
			var me = this;
			var userInfo = me.getCurrentUserInfo();
			if (userInfo != null) {
				var currentProjectInfo = me.getCurrentProjectInfo();
				if (currentProjectInfo != null){
					//me.currentProjectInfo = currentProjectInfo;
					var piConfig = me.getPiConfig();
					if(piConfig != null){
						var maxShiftNumber = 0;
						if (currentProjectInfo.IsSecondEnabled == 1){
							maxShiftNumber = 1;
						}
						if (currentProjectInfo.IsThirdEnabled == 1){
							maxShiftNumber = 2;
						}
						if(piConfig.piShit > maxShiftNumber){
							//去Config页面
							uni.reLaunch({
								url: 'piconfig'
							});
						} else if (piConfig.piShit == 0){
							uni.reLaunch({
								url: 'pifirst'
							});
						} else if(piConfig.piShit == 1){
							uni.reLaunch({
								url: 'pisecond'
							});
						} else {
							uni.reLaunch({
								url: 'pithird'
							});
						}
					} else {
						//去Config页面
						uni.reLaunch({
							url: 'piconfig'
						});
					}
				} else {
					//未选择项目，返回main选择项目
					uni.reLaunch({
						url: '../main/main'
					});
				}
			} else {
				//未登录，返回登陆界面
				uni.reLaunch({
					url: '../user/login'
				});
			}
		},
		methods: {
			
		}
	}
</script>

